html {
  height: 100%; }

body {
  color: linear-gradient(to right, #ad5389, #3c1053);
  background: linear-gradient(to right, #ad5389, #3c1053);
  font-family: Arial, Helvetica, sans-serif; }

.title {
  text-align: center; }

#board {
  position: relative;
  max-width: 100%;
  margin: auto;
  border: 2px solid #ffffff;
  background: #ffffff63; }

.square {
  display: inline-block;
  background-color: #0000004f;
  text-align: center;
  border: 0.5px solid #ffffff;
  box-sizing: border-box;
  position: absolute;
  font-size: 40px; }

.square-number {
  position: absolute;
  display: inline-block;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 60px;
  color: #ffffff;
  user-select: none;
  pointer-events: none; }

.game-data {
  color: #fff;
  max-width: 604px;
  margin: auto;
  display: flex;
  justify-content: space-between; }
  .game-data .movements, .game-data .timer {
    max-width: 604px;
    display: inline-block; }
    .game-data .movements .name, .game-data .timer .name {
      display: inline-block; }
    .game-data .movements .value, .game-data .timer .value {
      display: inline-block; }

.game-buttons {
  text-align: center;
  margin-top: 10px; }
  .game-buttons .new-game-btn {
    font-size: 25px;
    border-radius: 8px;
    border: 0.5px solid #ffffff;
    background: #ffffff87;
    color: #ffffff;
    padding: 2px 10px;
    height: 34px;
    box-sizing: border-box; }
  .game-buttons .puzzle-size-container {
    display: inline-block;
    font-size: 25px;
    line-height: 30px;
    border-radius: 8px;
    border: 0.5px solid #ffffff;
    background: #ffffff87;
    color: #ffffff;
    padding: 2px 10px;
    vertical-align: top;
    position: relative;
    height: 34px;
    box-sizing: border-box; }
    .game-buttons .puzzle-size-container select {
      font-size: 20px;
      margin-bottom: 1px;
      vertical-align: bottom; }

.win-text-container {
  position: absolute;
  width: 100%;
  height: 100%;
  background: #000000c9; }
  .win-text-container .win-text {
    font-size: 100px;
    color: #39b907;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%); }

@media only screen and (max-width: 500px) {
  .game-data h1 {
    font-size: 16px; }
  .square-number {
    font-size: 20px; }
  .game-buttons .new-game-btn {
    font-size: 16px;
    height: 30px;
    margin: 6px; }
  .game-buttons .puzzle-size-container {
    font-size: 16px;
    line-height: 24px;
    height: 30px;
    margin: 6px; }
    .game-buttons .puzzle-size-container select {
      font-size: 16px;
      height: 24px; } }
